'use client';
import React, { useState } from 'react';
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet';
import 'leaflet/dist/leaflet.css';
import L from 'leaflet';

// Fix default icon bug in Leaflet + Next.js
delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
  iconRetinaUrl: '/leaflet/marker-icon-2x.png',
  iconUrl: '/leaflet/marker-icon.png',
  shadowUrl: '/leaflet/marker-shadow.png',
});

const units = [
  { name: "Portsmouth Unit", lat: 50.7989, lng: -1.0912 },
  { name: "Brighton Unit", lat: 50.8225, lng: -0.1372 },
  { name: "London Unit", lat: 51.5074, lng: -0.1278 },
];

const FindShipSection = () => {
  const [search, setSearch] = useState('');

  return (
    <section className="bg-white py-16 px-4 md:px-8">
      <div className="max-w-5xl mx-auto text-center">
        <h2 className="text-3xl font-bold text-blue-800 mb-6">Find Your Nearest Unit</h2>
        <p className="text-gray-700 mb-6">Search by city name or explore directly on the map.</p>

        <input
          type="text"
          placeholder="Search (for now, just visual)"
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          className="border border-gray-300 rounded px-4 py-2 w-full md:w-1/2 mb-6"
        />

        <MapContainer
          center={[51.5074, -0.1278]}
          zoom={6}
          scrollWheelZoom={true}
          className="w-full h-[500px] rounded shadow"
        >
          <TileLayer
            attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          />
          {units.map((unit, idx) => (
            <Marker key={idx} position={[unit.lat, unit.lng]}>
              <Popup>{unit.name}</Popup>
            </Marker>
          ))}
        </MapContainer>
      </div>
    </section>
  );
};

export default FindShipSection;
